@use "@/scss/utils/mixins" as *;

.Footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #6d7a88;
}
.FooterDescription {
  height: min-content;
  padding-left: 20px;
  color: #6d7a88;
  max-width: 420px;
  font-size: 14px;
  font-weight: 400;
  line-height: 21px;
  border-left: 3px solid #fd413c;
}

@include mobile {
  .noneForMobile {
    display: none;
  }
}
